*, *:before, *:after {
	box-sizing: border-box;
}
@font-face {
	font-family: 'Lato';
	src: url('../../../fonts/Lato-Light.ttf');
	font-weight: 300;
}
@font-face {
	font-family: 'Lato';
	src: url('../../../fonts/Lato-LightItalic.ttf');
	font-weight: 300;
	font-style: italic;
}

@mixin flex-sp-betw {
	display: flex;
	justify-content: space-between;
}

@mixin ab-center {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}
body {
	color: #000;
	font-weight: 300;
	font-family: 'Lato', sans-serif;
}
body._ffn_page ._ffn_textNode {
	font-family: 'Lato', sans-serif;
}
main {
	padding-top: 100px;
	margin-bottom: 125px;
}
a {
	color: #000;
	text-decoration: none;
}
h2 {
	font-size: 24px;
}
img {
	max-width: 100%;
	height: auto;
}
