.bgimg {
	background-position: 50% 50%;
	background-size: cover;
	background-repeat: no-repeat;
}
.btn {
	padding-top: 15px;
	padding-bottom: 15px;
	font: {
		size: 16px;
		weight: 300;
	}
	text-transform: uppercase;
	border: 1px solid #000;
	cursor: pointer;
	background-color: transparent;
	outline: none;
}
.logo__img {
	height: 20px;
	width: auto;
}
.main-link {
	padding-top: 32px;
	padding-bottom: 32px;
	font-size: 24px;
	background-color: #FFF;
}
.col-low {
	padding-top: 20px;
}
.fl-wrap {
	@include flex-sp-betw;
	flex-wrap: wrap;
}
.slick-slide img {
	margin: 0 auto;
}